@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.header{
    display: flex;
    align-items: center;
    font-size: 14px;
    padding: 0 0 0 10px;
    margin: -8px;
    position: -webkit-sticky;
    position: sticky;
    color: #606060;
    top: 0;
    z-index: 100;
    background:white;
    justify-content: space-between;
}

.header > .header_left {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.header > .header_left > img {
    height: 35px;
    margin-left: 15px;
    margin-right: 5px;
}

.header > .header_right {
    display: flex;
    align-items: center;
    font-size: 10px;
    /* padding-right: 20px; */
}

.header > .header_right > .header_right_icons {
    margin-right: 20px;
    color: #767676;
    cursor: pointer;
}

hr {
    height:1px;
    border: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    background: lightgrey;
}
.createNote {
    display: flex;
    justify-content: center;
}

.createNote > form{
    display: flex;
    flex-direction: column;
    box-shadow: 2px 2px 6px 0px rgba(0,0,0,0.75);
    border: 1px solid lightgrey;
    border-radius:16px;
    margin: 20px auto;
}

.createNote > form > input {
    outline: none;
    border:none;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
    border-bottom: 1px solid lightgrey;
    height: 46px;
    width: 600px;
    font-weight: bolder;
    font-size:18px;
    padding: 10px;
}

.createNote > form > textarea {
    outline: none;
    border: none;
    font-weight: normal;
    font-size:18px;
    padding: 10px;
}
.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 10px !important;
    margin-top:230px;
    color: #606060;
    position: -webkit-sticky;
    position: sticky;
    background: #f9f9f9;
    z-index: 50;
    bottom: 0;
}

.footer > p:hover {
    color: black;
    cursor: pointer;
}

.footer > .timeGiver{
    cursor: default;
}
.note {
    border: 1px solid lightgrey;
    border-radius: 8px;
    margin: 20px 20px;
    width: 260px;
    text-align: center;
}
.app_content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
*{
    font-family: 'Roboto', sans-serif;
}
